import React, { Component } from 'react';
import { 
  Button,
  Card, 
  CardBody, 
  CardHeader, 
  Col, 
  Row, 
  Fade,
  Collapse,
  Input,
  FormGroup,
  Label
} from 'reactstrap';
import TinyMCE from 'react-tinymce';
import { history } from '../../helpers/history';
import { connect } from 'react-redux';
import { createData } from '../../redux/berita/beritaAction';
import { getData } from '../../redux/category/categoryAction';
import { status } from '../../root/config';

class Tables extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.toggleFade = this.toggleFade.bind(this);
        this.state = {
            collapse: true,
            fadeIn: true,
            timeout: 300,
            dataCategory: [],
            title: '',
            content: '',
            category: '',
            author: '',
            image: '',
            status: '',
        };
        this.handleClick = this.handleClick.bind(this);
        this.handleCancel = this.handleCancel.bind(this);
        this.handleEditor = this.handleEditor.bind(this);
    }

    async componentWillMount(){
        await this.props.dispatch(getData());
    }

    async componentWillReceiveProps(nextProps){
        const newDataCategory = await nextProps.data.data.data;
        this.setState({
            dataCategory: newDataCategory,
        })
    }

    handleClick = event => {
        event.preventDefault();
        const data = new FormData();
        data.append('title', this.state.title);
        data.append('content', this.state.content);
        data.append('id_category', this.state.category);
        data.append('author', this.state.author);
        data.append('images', this.state.image);
        data.append('status', this.state.status);

        const config = {
            headers: {
                'content-type': 'multipart/form-data'
            }
        }
        
        this.props.dispatch(createData(data, config));
    };

    handleEditor(e){
        const Content = e.target.getContent();
        this.setState({ content: Content })
    }

    handleChange = prop => event => {
        this.setState({ [prop]: event.target.value });
    }

    handleFileChange = prop => event => {
        this.setState({
          image: event.target.files[0]
        })
    }

    handleCancel() {
        history.push('/berita');
    }

    toggle() {
      this.setState({ collapse: !this.state.collapse });
    }

    toggleFade() {
      this.setState((prevState) => { return { fadeIn: !prevState }});
    }
  
    render() {
        return (
          <div className="animated fadeIn">
            <Row>
              <Col xs="12">
                <Fade timeout={this.state.timeout} in={this.state.fadeIn}>
                  <Card>
                    <CardHeader>
                      <i className="fa fa-edit"></i> Tambah Berita
                      <div className="card-header-actions">
                        <Button color="link" className="card-header-action btn-setting"><i className="icon-settings"></i></Button>
                        <Button color="link" className="card-header-action btn-minimize" data-target="#collapseExample" onClick={this.toggle}><i className="icon-arrow-up"></i></Button>
                      </div>
                    </CardHeader>
                    <Collapse isOpen={this.state.collapse} id="collapseExample">
                      <CardBody>
                        <form onSubmit={this.handleClick} noValidate>
                            <div className="row">
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleEmail">Title</Label>
                                      <Input 
                                        type="text" 
                                        name="title" 
                                        onChange={this.handleChange('title')} 
                                        value={this.state.title}
                                        placeholder="Title" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12">
                                    <br/>
                                    <FormGroup>
                                        <Label for="exampleEmail">Content</Label>
                                        <TinyMCE
                                            content={this.state.content}
                                            config={{
                                                height: 500,
                                                paste_data_images: true,
                                                plugins: [
                                                    'advlist autolink lists link image charmap print preview anchor',
                                                    'searchreplace wordcount visualblocks code fullscreen',
                                                    'insertdatetime media table contextmenu paste code'
                                                ],
                                                toolbar: 'undo redo | bold italic | image code | alignleft aligncenter alignright | code',
                                                file_picker_types: 'file image media',
                                                pastes_data_images: true,
                                                file_browser_callback_types: 'image',
                                                selector: 'textarea#local-upload',  // change this value according to your HTML
                                                images_upload_url: 'postAcceptor.php',
                                                images_upload_handler: function (blobInfo, success, failure) {
                                                  setTimeout(function () {
                                                    /* no matter what you upload, we will turn it into TinyMCE logo :)*/
                                                    success('http://moxiecode.cachefly.net/tinymce/v9/images/logo.png');
                                                  }, 2000);
                                                },
                                                file_picker_callback: function (callback, value, meta) {
                                                    if (meta.filetype === 'file') {
                                                      //callback('mypage.html', {text: 'My text'});
                                                    }
                                                    if (meta.filetype === 'image') {
                                                      // callback('image.jpg', {alt: ''});
                                                    }
                                                    if (meta.filetype === 'media') {
                                                      //callback('movie.mp4', {source2: 'alt.ogg', poster: 'image.jpg'});
                                                    }
                                                }
                                            }}
                                            onChange={this.handleEditor}
                                        />
                                    </FormGroup>
                                    <br/>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12">
                                    <FormGroup>
                                        <Label for="exampleEmail">Category</Label>
                                        <Input 
                                            type="select" 
                                            name="category" 
                                            id="select" 
                                            value={this.state.category || ""}
                                            onChange={this.handleChange('category')}
                                        >
                                            <option> Silakan Pilih Category </option>
                                            {(this.state.dataCategory || []).map(item => (
                                                <option 
                                                    key={item.id} 
                                                    value={item.id}
                                                > 
                                                    {item.nama_category} 
                                                </option>
                                            ))}                                            
                                        </Input>
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleEmail">Author</Label>
                                      <Input 
                                        type="text" 
                                        name="author" 
                                        onChange={this.handleChange('author')} 
                                        value={this.state.author}
                                        placeholder="Author" />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                      <Label for="exampleFile">File Image</Label>
                                      <Input 
                                        type="file" 
                                        name="image" 
                                        onChange={this.handleFileChange()} />
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12"> 
                                    <FormGroup>
                                        <Label for="exampleEmail">Status</Label>
                                        <Input 
                                            type="select" 
                                            name="status" 
                                            id="select" 
                                            value={this.state.status || ""}
                                            onChange={this.handleChange('status')}
                                        >
                                            {status.map(item => (
                                                <option 
                                                    key={item.value} 
                                                    value={item.value}
                                                > 
                                                    {item.label} 
                                                </option>
                                            ))}                                            
                                        </Input>
                                    </FormGroup>
                                </div>
                                <div className="col-sm-12 col-lg-12 col-md-12">
                                    <div style={{ float: 'right' }}>
                                        <button 
                                            type="submit" 
                                            className="btn btn-primary data-submit" 
                                        >
                                            Submit
                                        </button>
                                        &nbsp;
                                        <button
                                            className="btn btn-danger"
                                            onClick={this.handleCancel}
                                        >
                                            Cancel
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </form>
                      </CardBody>
                    </Collapse>
                  </Card>
                </Fade>
              </Col>
            </Row>
          </div>    
        );
    }
}

const mapStateToProps = (state) => {
  return {
    data: state.dataCategory,
  };
}

// const mapDispatchToProps = (dispatch) => {
//   return {
//     actions: bindActionCreators(createData, dispatch)
//   };
// }

export default connect(mapStateToProps)(Tables);